export const selectStudent: StudentSelector<IEditingResourceState<IStudent>> = (state) => state.editableStudent;
export const selectStudentData: StudentSelector<IStudent> = (state) => selectStudent(state).data;
export const selectStudentEditingFields: StudentSelector<editingFields> = (state) => selectStudent(state).fieldsEditing;

export const selectSudentName: StudentSelector<string> = (state) => {
  const studentName = selectStudentData(state).name;
  return studentName;
};

const isStudentFieldEditingSelectorCreator: ArgumentSelector<StudentSelector<boolean>> = (fieldName: string) => {
  return (state) => selectStudentEditingFields(state)[fieldName];
}

export const selectIsStudentNameEditing: StudentSelector<boolean> = isStudentFieldEditingSelectorCreator('name');


export const selectStudentDescription: StudentSelector<string> = (state) => {

  // const studentDescription = selectStudentData(state).description;

  return textL;
};

export const selectIsStudentDescriptionEditing: StudentSelector<boolean> = isStudentFieldEditingSelectorCreator('description');

export const selectStudentAvatarUrl: StudentSelector<string> = (state) => {

  const avatarUrl = selectStudentData(state).avatar;

  return avatarUrl;
}

export const selectStudentAbonements: StudentSelector<string[]> = (state) => selectStudentData(state).abonementConsumables;

export const selectStudentAbonementsLength: StudentSelector<number> = (state) => {
  
    const abonementOffersLength = selectStudentAbonements(state).length;
  
    return abonementOffersLength;
  }

export const selectStudentSubjects: StudentSelector<string[]> = (state) => {
  const studentConsumable = selectStudentAbonementConsumables(state);
  const allSsubjects = studentConsumable.flatMap((consumable) => consumable.subjects);
  const uniqueSubjects = [...new Set(allSsubjects)];
  return uniqueSubjects;
};

export const selectStudentSubjectsLength: StudentSelector<number> = (state) => {
  
    const subjectsLength = selectStudentSubjects(state).length;
  
    return subjectsLength;
  }
  
  export const selectStudentSubjectViews: StudentSelector<ISubjectView[]> = (state) => {

    // const studentSubjectsIds = selectStudentSubjects(state);

    return dummySubjectViews
  };

  export const selectStudentAbonementConsumables: StudentSelector<IAbonementConsumable[]> = (state) => {

    // const studentAbonementIds = selectStudentAbonements(state);

    return dummyAbonementConsumables
  }

  export const selectStudentAbonementsSubjectsFormatted: StudentSelector<Map<id, string>> = (state) => {
    const idNameMap = new Map<id, string>();
    const abonementOffers = selectStudentAbonementConsumables(state);
    const subjects = selectStudentSubjectViews(state);
  
    abonementOffers.forEach((abonementOffer) => {
      const thisAbonementOfferSubjects = subjects.filter((subject) => abonementOffer.subjects.includes(subject.id));
      const thisAbonementOfferSubjectsNames = thisAbonementOfferSubjects.map((subject) => subject.name);
      const subjectsFormatted = thisAbonementOfferSubjectsNames.join(', ');
      idNameMap.set(abonementOffer.id, subjectsFormatted);
    });
  
    return idNameMap;
  }

