import { PayloadAction, createSlice } from "@reduxjs/toolkit";

export enum StudentView {
  schedule = 'schedule',
  studentProfilePage = 'studentProfilePage',
  searchPage = 'searchPage',
  abonementPage = 'abonementPage',
  abonementShowcasePage = 'abonementShowcasePage',
}

export enum ScheduleView {
  schedule = 'schedule',
  week = 'week',
}

export enum StudentProfilePageView {
  subjects = 'subjects',
  abonements = 'abonements',
}

export enum AbonementPageView {
  subjects = 'subjects',
  lessons = 'lessons',
}

export enum ModalType {
  closed = 'closed',
  teacherPageForStudent = 'teacherPageForStudent',
  subjectPageForStudent = 'subjectPageForStudent',
}

export interface IStudentModalState {
  studentView: StudentView;
  scheduleView: ScheduleView;
  studentProfilePageView: StudentProfilePageView;
  abonementPageView: AbonementPageView;
  modalMode: ModalType;
  isSudentDescriptionShowMore: boolean;
  isTeacherForStudentDescriptionShowMore: boolean;
  isSubjectForStudentDescriptionShowMore: boolean;
  currentDate: Date;
};

const studentModalInitialState: IStudentModalState = {
  studentView: StudentView.schedule,
  scheduleView: ScheduleView.schedule,
  studentProfilePageView: StudentProfilePageView.subjects,
  abonementPageView: AbonementPageView.lessons,
  modalMode: ModalType.closed,
  isSudentDescriptionShowMore: false,
  isTeacherForStudentDescriptionShowMore: false,
  isSubjectForStudentDescriptionShowMore: false,
  currentDate: new Date(),
};

export const teacherModalSlice = createSlice({
  name: 'teacherModal',
  initialState: studentModalInitialState,
  reducers: {
    setModalState: (state, { payload: stateDiff }: PayloadAction<Partial<IStudentModalState>>) => ({
      ...state,
      ...stateDiff,
    }),
    resetModalState: () => ({...studentModalInitialState}),
  },
});
