import { HOC } from "../../../common/hoc/types";
import { selectStudentProfilePageView } from "../../selectors/novigation";
import { useStudentSelector } from "../../store";
import { StudentProfilePageView } from "../../store/modal";
import { StudentAbonementList } from "../StudentAbonementList";
import { StudentSubjectList } from "../StudentSubjectList";

export const StudentProfileWindow: HOC = () => {

  const studentProfilePageView = useStudentSelector(selectStudentProfilePageView);
    
  switch (studentProfilePageView) {
    case StudentProfilePageView.subjects: {
      return (
        <StudentSubjectList />
      )
    }
    case StudentProfilePageView.abonements: {
      return (
        <StudentAbonementList />
      )
    }
    default: return <></>;
  }
}
