import { useEffect, useRef } from "react";
import { HOC } from "../../../common/hoc/types";
import { selectIsSudentDescriptionShowMore } from "../../selectors/novigation";
import { useStudentSelector } from "../../store";
import React from "react";
import { DescriptionContainer, MetricItemContainer, MetricsContainer, ProfileWrapper } from "../../../teacher/hoc/TeacherProfile";
import { Typography } from "@mui/material";
import { 
  selectIsStudentDescriptionEditing, 
  selectStudentAvatarUrl, 
  selectStudentDescription, 
} from "../../selectors/editable";
import { selectStudentDescriptionHeight } from "../../selectors/dom";
import { avatarHeight, descriptionDefaultHeight, photoHeight } from "../../../teacher/selectors/dom";
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import { TextButton } from "../../../common/component/TextButton";
import { ClickOutsideHandler } from "../../../common/component/ClickOutsideHandler";
import { TextField, TextFieldType } from "../../../common/component/TextField";
import { Avatar } from "../../../common/component/Avatar";


export const StudentProfile: HOC = () => {

    const subjectsNumber = 28;
    const abonementsNumber = 4;
    // const subjectsNumber = useStudentSelector(selectStudentSubjectsLength);
    // const abonementsNumber = useStudentSelector(selectStudentAbonementsLength);
    const photoUrl = useStudentSelector(selectStudentAvatarUrl);
    const profileDescription = useStudentSelector(selectStudentDescription);
    const isDescriptionEditing = useStudentSelector(selectIsStudentDescriptionEditing);
    const showMore = useStudentSelector(selectIsSudentDescriptionShowMore);
    const descriptionHeight = useStudentSelector(selectStudentDescriptionHeight);


    // const dispatch = useDispatch();

    const descriptionContainerRef = useRef<HTMLDivElement | null>(null);
    const [hasShowMore, setHasShowMore] = React.useState(false);

    useEffect(() => {
        if (descriptionContainerRef.current) {
          const container = descriptionContainerRef.current;
          const contentHeight = container.scrollHeight;
      
          setHasShowMore( contentHeight > descriptionDefaultHeight);
          container.scrollTop = 0;
        }
      }, [profileDescription, showMore, isDescriptionEditing]);

    return (
        <ProfileWrapper minHeight={photoHeight + descriptionHeight}>
            <MetricsContainer height={photoHeight}>
                <MetricItemContainer>
                    <Avatar image={photoUrl} size={avatarHeight} onClick={() => undefined} />
                </MetricItemContainer>
                <MetricItemContainer>
                    <Typography textAlign="center">{subjectsNumber}</Typography>
                    <Typography textAlign="center">Subjects</Typography>
                </MetricItemContainer>
                <MetricItemContainer>
                    <Typography textAlign="center">{abonementsNumber}</Typography>
                    <Typography textAlign="center">Abonements</Typography>
                </MetricItemContainer>
            </MetricsContainer>
            <ClickOutsideHandler onClickOutside={() => console.log('confirmChangeStudentDescription')}>
                <DescriptionContainer
                    ref={descriptionContainerRef}
                    height={descriptionHeight}
                    maxHeight={ descriptionHeight }
                    overflow={ showMore ? "auto" : "hidden" }
                    onClick={ () => console.log('startChangeStudentDescription') }
                >
                    {isDescriptionEditing ? 
                        <TextField 
                            type={TextFieldType.textArea}
                            text={profileDescription}
                            onChange={(e) => console.log('changeStudentDescription')}
                        /> :
                        <Typography
                        style={{ wordWrap: 'break-word' }}
                        gutterBottom
                        onClick={() => console.log('startChangeStudentDescription')}
                        >
                            {profileDescription ? profileDescription : 'add your description'}
                        </Typography>
                    }
                </DescriptionContainer>
            </ClickOutsideHandler>
            {(hasShowMore && !isDescriptionEditing) && (
                <TextButton
                    title='See more'
                    onClick={() => console.log('studentShowMore')}
                >
                    { showMore ? <KeyboardArrowUpIcon  /> : <KeyboardArrowDownIcon /> }
                </TextButton>
            )}
        </ProfileWrapper>
    );
}
    