import { HOC } from "../../../common/hoc/types";
import { selectSearchPageModalMode } from "../../selectors/novigation";
import { useStudentSelector } from "../../store";
import { ModalType } from "../../store/modal";
import { SubjectPageForStudentModal } from "../SubjectPageForStudentModal";
import { TeacherPageForStudentModal } from "../TeacherPageForStudentModal";

export const SearchPageModal: HOC = () => {

  const modalMode = useStudentSelector(selectSearchPageModalMode);

  switch (modalMode) {
    case ModalType.subjectPageForStudent: {
      return <SubjectPageForStudentModal />
    };
    case ModalType.teacherPageForStudent: {
      return <TeacherPageForStudentModal />
    };
    default: return <></>
  };
};
