import { useEffect, useRef } from "react";
import { HOC } from "../../../common/hoc/types";
import React from "react";
import { textL } from "../../../common/component/dummies";
import { descriptionDefaultHeight, selectSubjectDescriptionHeight } from "../../../teacher/selectors/dom";
import { useStudentSelector } from "../../store";
import { selectIsSubjectForStudentDescriptionShowMore } from "../../selectors/novigation";
import { Box, Typography } from "@mui/material";
import { DescriptionContainer } from "../../../teacher/hoc/TeacherProfile";
import { TextButton } from "../../../common/component/TextButton";
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';

export const SubjectDescriptionForStudent: HOC = () => {
  
  const descriptionContainerRef = useRef<HTMLDivElement | null>(null);
  const [hasShowMore, setHasShowMore] = React.useState(false);

  const subjectDescription = textL;

  const isShowMore = useStudentSelector(selectIsSubjectForStudentDescriptionShowMore);
  const descriptionHeight = useStudentSelector(selectSubjectDescriptionHeight);


  useEffect(() => {
    if (descriptionContainerRef.current) {
      const container = descriptionContainerRef.current;
      const contentHeight = container.scrollHeight;
  
      setHasShowMore(contentHeight > descriptionDefaultHeight);
      container.scrollTop = 0;
    }
  }, [subjectDescription, isShowMore]);

  return (
    <Box >
    <DescriptionContainer
      ref={descriptionContainerRef}
      height={descriptionHeight}
      maxHeight={ descriptionHeight }
      overflow={ isShowMore ? "auto" : "hidden" }
      onClick={ () => console.log('click') }
    >
      <Typography
        style={{ wordWrap: 'break-word' }}
        gutterBottom
        onClick={()=>console.log('click')}
      >
        {subjectDescription ? subjectDescription : ''}
      </Typography>
    </DescriptionContainer>
      {(hasShowMore && !isShowMore) && (
        <TextButton
          title='See more'
          onClick={() => console.log('See more')}
        >
          { isShowMore ? <KeyboardArrowUpIcon  /> : <KeyboardArrowDownIcon /> }
        </TextButton>
      )}
    </Box>
  );
}
