import { HOC } from '../../../common/hoc/types';
import { selectStudentView } from '../../selectors/novigation';
import { StudentView } from '../../store/modal';
import { StudentProfilePage } from '../StudentProfilePage';
import { SearchPage } from '../SearchPage';
import { AbonementPage } from '../AbonementPage';
import { AbonementShowcase } from '../AbonementShowcase';
import { useStudentSelector } from '../../store';
import { StudentFooter } from '../StudentFooter';
import { StudentSchedule } from '../StudentSchedule';

export const StudentApp: HOC = () => {
  return (
    <>
      <StudentWindow />
      <StudentFooter/>
    </>
  );
}

const StudentWindow: HOC = () => {

  const navigation: StudentView = useStudentSelector(selectStudentView);

  switch (navigation) {
      case StudentView.schedule: {
          return  <StudentSchedule/>
      }
      case StudentView.studentProfilePage: {
          return <StudentProfilePage />
      }
      case StudentView.searchPage: {
          return <SearchPage />
      }
      case StudentView.abonementPage: {
          return <AbonementPage />
      }
      case StudentView.abonementShowcasePage: {
          return <AbonementShowcase />
      }
      default: return <></>
  }
}
