import { FullCalendarView } from "../../common/component/Calendar";
import {
  StudentProfilePageView, 
  AbonementPageView, 
  ModalType, 
  StudentView, 
  ScheduleView 
} from "../store/modal";
import { StudentSelector } from "./types";


export const selectStudentView: StudentSelector<StudentView> = (state) => state.modal.studentView;
export const selectStudentProfilePageView: StudentSelector<StudentProfilePageView> = (state) => state.modal.studentProfilePageView;
export const selectAbonementPageView: StudentSelector<AbonementPageView> = (state) => state.modal.abonementPageView;

export const selectSearchPageModalMode: StudentSelector<ModalType> = (state) => state.modal.modalMode;

export const selectIsSudentDescriptionShowMore: StudentSelector<boolean> = (state) => state.modal.isSudentDescriptionShowMore;
export const selectIsTeacherForStudentDescriptionShowMore: StudentSelector<boolean> = (state) => state.modal.isTeacherForStudentDescriptionShowMore;
export const selectIsSubjectForStudentDescriptionShowMore: StudentSelector<boolean> = (state) => state.modal.isSubjectForStudentDescriptionShowMore;

export const selectCurrentDate: StudentSelector<Date> = (state) => state.modal.currentDate;
export const selectScheduleView: StudentSelector<ScheduleView> = (state) => state.modal.scheduleView;

export const selectStudentFullCalendarView: StudentSelector<FullCalendarView> = (state) => {

  const scheduleView = selectScheduleView(state);

  switch (scheduleView) {
    case ScheduleView.schedule:
      return FullCalendarView.listWeek;;
    case ScheduleView.week:
      return FullCalendarView.timeGridWeek;
    default:
      return FullCalendarView.timeGridWeek;
  }
};
