import { descriptionDefaultHeight, showMoreDescriptionHeight } from "../../teacher/selectors/dom";
import { selectIsSubjectForStudentDescriptionShowMore, selectIsSudentDescriptionShowMore, selectIsTeacherForStudentDescriptionShowMore } from "./novigation";
import { StudentSelector } from "./types";

export const selectStudentDescriptionHeight: StudentSelector<number> = (state) => {
  const showMore = selectIsSudentDescriptionShowMore(state);
  const descriptionHeight = showMore ? showMoreDescriptionHeight : descriptionDefaultHeight;
  return descriptionHeight;
};


export const selectTeacherForStudentDescriptionHeight: StudentSelector<number> = (state) => {
  const showMore = selectIsTeacherForStudentDescriptionShowMore(state);
  const descriptionHeight = showMore ? showMoreDescriptionHeight : descriptionDefaultHeight;
  return descriptionHeight;
};

export const selectSubjectForStudentDescriptionHeight: StudentSelector<number> = (state) => {
  const showMore = selectIsSubjectForStudentDescriptionShowMore(state);
  const descriptionHeight = showMore ? showMoreDescriptionHeight : descriptionDefaultHeight;
  return descriptionHeight;
};

